<template>
    <div>
        <div class="panel panel-info">
            <div class="panel-body">
                <div>用户管理</div>
                <div class="buttonbox" v-show="!inforadmin">
                     <div class="col-sm-1" v-if="hasLoginComponents('sys:userMan:userOpera')">
                        <select class="form-control" v-model="tableSelectData.pageloginData.state" @change="changeAudit">
                            <option value="0">未审核</option>
                            <option value="1">已审核</option>
                        </select>
                    </div>
                    <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="hasLoginComponents('sys:userMan:roleOpera')&&tableSelectData.pageloginData.state==0">审核</button>
                    <button type="button" class="btn btn-warning" @click="updateData" v-if="hasLoginComponents('sys:userMan:roleOpera')&&tableSelectData.pageloginData.state==1">退审</button>
                    <button type="button" class="btn btn-danger" @click="deleteData" v-if="hasLoginComponents('sys:userMan:userOpera')">删除</button>
                    <div class="pull-right col-sm-3 search-input" v-if="hasLoginComponents('sys:userMan:search')">
                        <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                        <input type="text" class="form-control" placeholder="搜索用户"> 
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                        </div>
                    </div>
                </div>
                <tables  :statetitle="statetitle" :state="stateid" :tableSelectData="tableSelectData"></tables>
            </div>
        </div>
    </div>
</template>
<script>
import tables from "@/components/table.vue";
import addInfor from "@/components/addinfor.vue";
export default {
  data: function() {
    return {
      inforadmin: false,
      searchData: { data: "" },
      tableSelectData: {
        pageloginData: {
          id: this.stateid,
          state: 0
        },
        selectTableurl: "selectUserPaging.action"
      }
    };
  },
  props: {
    stateid: String,
    statetitle: String
  },
  components: {
    tables,
    addInfor
  },
  methods: {
    onTableClick(data) {
      this.checkData = data;
    },
    //判断是否当前页面是否存在按钮权限
    hasLoginComponents(perm) {
      var vm = this;
      return vm.$api.opreate.hasLoginComponents(vm, perm);
    },
    inforAddClick(bool) {
      var vm = this;
      var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
      if (vm.$api.opreate.TESTcheck(vm, "审核")) {
        var datas = {
          data: checkData[0],
          states: {
            id: vm.stateId
          }
        };
        vm.$api.http.opreateHttp(vm, "examineUser.action", datas, "审核");
      }
    },
    // 修改按钮按下
    updateData() {
      var vm = this;
      var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
      if (vm.$api.opreate.TESTcheck(vm, "退审")) {
        var datas = {
          data: checkData[0],
          states: {
            id: vm.stateid
          }
        };
        vm.$api.http.opreateHttp(vm, "frozenUser.action", datas, "退审");
      }
    },
    // 删除按钮按下
    deleteData() {
      var vm = this;
      var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
      if (vm.$api.opreate.TESTcheck(vm, "删除")) {
        //询问框

        layer.confirm(
          "是否删除",
          {
            btn: ["确认", "取消"] //按钮
          },
          function() {
            var datas = {
              id: checkData[0].id,
              states: {
                id: vm.stateId
              }
            };
            vm.$api.http.opreateHttp(
              vm,
              "deleteUserData.action",
              datas,
              "删除"
            );
          }
        );
      }
    },
    //搜索按钮按下
    searchBtnClick() {
      var vm = this;
      if (vm.searchData.data != "") {
      } else {
        vm.$layer.msg("请输入用户名后搜索", { icon: 0 });
      }
    },
    //调换审核状态查看
    changeAudit() {
      var vm = this;
      vm.$store.commit("REININTPAGE");
      vm.$api.http.loginTableData(
        vm,
        "selectUserPaging.action",
        vm.tableSelectData.pageloginData
      );
    }
  },
  watch: {
    stateid(news) {
      this.tableSelectData.pageloginData.id = news;
    }
  }
};
</script>

<style>
</style>



